<template>
   <div class="max-w-4xl bg-white p-8 mx-auto">
    <div class="p-4" v-for="list in lists" :key="list.id">
      <div class="text-4xl text-brand">
         {{ list.key }} 
      </div>
       <div class="text-left leading-10">
           {{ list.value }}
       </div>
      </div>
   </div>
</template>

<script>
export default {

   created(){
      this.$store.dispatch('legally_binding_contract/DataList');
   },
    computed:{
      lists(){
         return this.$store.state.legally_binding_contract.list;
      }
    }
}
</script>